.container {
  text-align: center;
  height: 100%;
}

.container input {
  width: 50%;
  max-width: 300px;
  padding: 20px;
  height: 50%;
  max-height: 150px;
  border: 1px solid #c7c7c7;
  border-radius: 3px;
  background: #e9f0f7;
  font-size: 30px;
  margin-top: 20px;
}

.hidden {
  display: none;
}

.logs {
  text-align: left;
  width: 80%;
  max-width: 500px;
  display: block;
  margin: 20px auto;
  padding: 20px;
  white-space: pre;
}
