.container {
  padding: 0;
  margin: 0 auto;
}

@media only screen and (max-width: 500px) {
  .container {
    width: 400px;
  }
  .diagram {
    transform: scale(0.8);
  }
}

@media only screen and (max-width: 400px) {
  .container {
    width: 250px;
  }
  .diagram {
    transform: scale(0.5);
  }
}

.diagram {
  width: 500px;
  margin: 0 auto;
  position: relative;
  transform-origin: top left;
}

.img {
  width: 100%;
  opacity: 0.5;
}

.people {
  position: absolute;
}

.device {
  position: absolute;
  font-size: 60px;
  cursor: pointer;
}

.device img {
  width: 50px;
}

.device .tooltip {
  font-size: 12px;
  visibility: hidden;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
}

.device:hover .tooltip {
  visibility: visible;
}
