.container {
  padding: 32px 0;
  margin: 0 auto;
  width: 250px;
}
.icon {
  margin: 0 0.5rem 1rem;
  font-size: 3rem;
  width: 4rem;
  height: 4rem;
  line-height: 4rem;
  border-radius: 100%;
  background-color: #fafbfc;
  fill: rgba(76, 76, 76, 0.35);
  transition: transform 200ms ease-out;
  box-shadow: inset 0 0 0.25rem 0 rgba(0, 0, 0, 0.08);
  display: inline-block;
  cursor: pointer;
}

.icon svg {
  transform: scale(0.8);
}

.selected {
  fill: #ffffff;
  transform: scale(1.25);
  box-shadow: none;
}

.disarmed {
  background-color: #1998d5;
}

.away,
.home {
  background-color: #e53935;
}

.subtext {
  text-align: center;
}
